


















































import Vue from "vue";
import ArchitectureController from "@/api/controllers/imaging/ArchitectureController";

export default Vue.extend({
  name: "DeleteArchitectureModal",

  props: {
    application: String,
    element: Object,
    dialog: Boolean
  },

  mounted() {
    this.refresh();
  },

  data() {
    return {
      // errors & loading merge
      loadingModify: false,
      errorModify: "",

      // errors & loading levels
      errorElementDelete: "",
      loadingdestinationModules: false
    };
  },

  methods: {
    async refresh() {
      this.errorModify = "";
      this.errorElementDelete = "";
    },

    querySelections(v) {
      this.items = this.destinationModules.filter(e => {
        return (
          (e.name || "").toLowerCase().indexOf((v || "").toLowerCase()) > -1
        );
      });
    },

    getSelectedName(selected) {
      return selected.type == "archimodel" ? "Architecture" : "Subset";
    },

    /**
     * Modify a module
     */
    async deleteElement() {
      if (this.element == null) return;

      try {
        this.loadingModify = true;

        await ArchitectureController.deleteArchitectureElement(
          this.application,
          this.element._id,
          this.element.type
        );
        this.errorModify = "";
        this.$emit("close");
      } catch (err) {
        console.error(`Failed to delete the element`, err);
        this.errorModify = `Failed to delete the element. ${err}`;
      } finally {
        this.loadingModify = false;
      }
    },

    /**
     * Close the modal
     */
    closeModal() {
      this.$emit("close");
    }
  }
});
