























































































import Vue from "vue";
import { AipConfiguration } from "@/api/interface/aip/Aipconfiguration";
import { AipConfigurationController } from "@/api/controllers/aip/AipConfigurationController";

export default Vue.extend({
  name: "AddAIPServerModal",

  props: {
    dialog: Boolean
  },

  methods: {
    closeModal() {
      this.dialog = false;
      this.$emit("close", false);
    },

    async save() {
      // implement
      this.loading = true;
      this.errors = "";

      try {
        await AipConfigurationController.createConfigurationNode(
          this.configuration
        );
      } catch (err) {
        console.error("Failed to create configuration.", err);
        this.errors = "Failed to create configuration. Error: " + err;
      } finally {
        this.loading = false;
      }

      this.closeModal();
    }
  },

  data: () => ({
    errors: "",
    loading: false,
    // implement
    configuration: {
      name: "Server Name",
      url: "http://localhost",
      port: 2284,
      database: "postgres",
      user: "operator",
      password: "CastAIP",
      schemas: []
    } as AipConfiguration
  })
});
