




























































































































































































































































































import { UseCaseController } from "@/api/controllers/extensions/paris/UseCaseController";
import { UseCase } from "@/api/interface/paris/useCase.interface";
import Vue from "vue";

export default Vue.extend({
  name: "UseCases",

  data: () => ({
    active: null,
    items: [] as UseCase[],
    open: null as UseCase,
    selected: null as UseCase,

    // Files
    files: [],

    editedItem: {
      id: null,
      title: "",
      description: "",
      categories: [],
      active: false,
      selected: false
    } as UseCase,

    defaultItem: {
      title: "",
      description: "",
      categories: [],
      active: false,
      selected: false
    } as UseCase,

    titleEditBox: "",
    contentDeleteBox: "",

    editParentUseCaseId: -1,
    parentUseCaseId: -1,
    editedIndex: 0,
    dialog: false,
    dialogDelete: false,
    loadingDelete: false,

    snackbarInfo: false,
    textSnackBar: ""
  }),

  methods: {
    async getChildren(item: UseCase): Promise<UseCase> {
      return await UseCaseController.getAttachedUseCase(item.id)
        .then(async (res: UseCase[]) => {
          item.children = res;

          // Treat childrent
          for (const i in item.children) {
            item.children[i].parentId = item.id;
            item.children[i] = await this.getChildren(item.children[i]);
          }

          return item;
        })
        .catch(err => {
          console.error(
            `Failed to fetch the children for use case with id : ${item.id}`,
            err
          );
          return item;
        });
    },

    getRootUseCase() {
      UseCaseController.getRootUseCase()
        .then(async (res: UseCase[]) => {
          if (res) {
            this.items = [];
            for (const i in res) {
              res[i].parentId = -1;
              this.items.push(await this.getChildren(res[i]));
            }
          }
        })
        .catch(err => {
          console.error("Failed to retrieve the root use cases", err);
        });
    },

    selectItem(item) {
      this.selected = item;
    },

    createItem() {
      this.titleEditBox = "Create new Use case";
      this.editedIndex = -1;
      this.parentUseCaseId = -1;
      this.editedItem = Object.assign({}, this.defaultItem);
      this.dialog = true;
    },

    createItemUnderParent(item) {
      this.titleEditBox = `Create new Use case under ${item.title}`;
      this.editedIndex = -1;
      this.parentUseCaseId = item.parentId || -1;
      this.editedItem = Object.assign({}, this.defaultItem);
      this.dialog = true;
    },

    editItem(item: UseCase) {
      this.titleEditBox = `Edit the use case : ${item.title}`;
      this.editedIndex = this.items.indexOf(item);
      this.parentUseCaseId = -1;
      this.editParentUseCaseId = item.parentId || -1;
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },

    deleteItem(item) {
      this.contentDeleteBox = `You're about to delete the use case with name <strong>${item.title}</strong>. Are you sure that you want to delete it ?  `;
      this.editedIndex = this.items.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialogDelete = true;
    },

    confirmDelete() {
      this.loadingDelete = true;
      UseCaseController.deleteUseCase(this.editedItem)
        .then((res: boolean) => {
          if (res) {
            this.textSnackBar = "Successfully deleted the use case.";
          } else {
            this.textSnackBar = "Failed to delete the use case.";
          }
          this.snackbarInfo = true;
          this.refresh();
        })
        .catch(err => {
          this.textSnackBar = `Failed to deleted the use case. Error: ${err}`;
          this.snackbarInfo = true;
        })
        .finally(() => {
          this.loadingDelete = false;
          this.closeDelete();
        });
    },

    close() {
      this.dialog = false;
      this.parentUseCaseId = -1;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    save() {
      if (this.editedItem.id && this.editedItem.id > 0) {
        UseCaseController.editUseCase(this.editedItem, this.editParentUseCaseId)
          .then((res: UseCase) => {
            this.textSnackBar = "Successfully updated the use case.";
            this.snackbarInfo = true;
            this.refresh();
          })
          .catch(err => {
            this.textSnackBar = `Failed to update the use case. Error: ${err}`;
            this.snackbarInfo = true;
          });
      } else if (this.parentUseCaseId != -1 && this.editedIndex <= -1) {
        UseCaseController.addUseCaseWithParent(
          this.editedItem,
          this.parentUseCaseId
        )
          .then((res: UseCase) => {
            this.textSnackBar = "Successfully attached a new use case.";
            this.snackbarInfo = true;
            this.refresh();
          })
          .catch(err => {
            this.textSnackBar = `Failed to attach the new use case. Error: ${err}`;
            this.snackbarInfo = true;
          });
      } else {
        UseCaseController.addUseCaseWithParent(
          this.editedItem,
          this.editParentUseCaseId
        )
          .then((res: UseCase) => {
            this.textSnackBar = "Successfully added the use case.";
            this.snackbarInfo = true;
            this.refresh();
          })
          .catch(err => {
            this.textSnackBar = `Failed to add the use case. Error: ${err}`;
            this.snackbarInfo = true;
          });
      }
      this.close();
    },

    refresh() {
      this.getRootUseCase();
    }
  },

  mounted() {
    this.refresh();
  }
});
